import profileInfoStyles from "./ProfileInfo.module.css";
import Preloader from "../../shared/Preloader.component/Preloader";
// import Status from "./Status/Status";
import StatusWHooks from "./Status/StatusWHooks";

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>;
    }
    return (
        <div>
            {/*<div>*/}
            {/*    <img className={profileInfoStyles.statusImage}*/}
            {/*         src="data:image/jpeg;base64,"/>*/}
            {/*</div>*/}
            <div>
                <img className={profileInfoStyles.userImage}
                     src={props.profile.photos.large ? props.profile.photos.large : "https://img.pravda.com/images/doc/c/5/c57f19f-bf6cc41-275622598-512933153533681-6934265882643283131-n.jpg"}
                     alt=""/>
                <StatusWHooks status={props.status} updateUserStatus={props.updateUserStatus}/>
                <p className={profileInfoStyles.description}>Description</p>
            </div>
        </div>
    );
};

export default ProfileInfo;
